.logo {
  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.logo:not([href]) {
  pointer-events: none;
}

.logo__img {
  width: 100px;
  height: 19px;

  @media (min-width: $tablet-width) {
    width: 166px;
    height: 41px;
  }

  @media (min-width: $desktop-width) {
    width: 200px;
    height: 51px;
  }
}
